











































































































/*-------- for touch screens --------*/
html {
  /*cursor: none;*/
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
a {
  /*cursor: none;*/
}

/*-------- fonts --------*/
@font-face {
  font-family: "Avenir-Medium";
  src: url("./assets/fonts/Avenir-Medium.ttf");
}
@font-face {
  font-family: "Avenir-Heavy";
  src: url("./assets/fonts/Avenir-Heavy.ttf");
}
@font-face {
  font-family: "Agenda-Bold";
  src: url("./assets/fonts/AgendaBold.ttf");
}
@font-face {
  font-family: "Futura-Bold";
  src: url("./assets/fonts/FuturaBold.otf");
}

/*-------- common styles --------*/
body {
  letter-spacing: 2px;
  font-family: "Avenir-Medium";
}
.image-frame {
  border: 2px;
  border-style: solid;
  border-color: #0a3c5b;
  overflow: hidden;
}
img.image-center {
  width: 100%;
  display: block;
  margin: auto;
}

/* additional */
.footer-notes {
  position: absolute;
  width: 100%;
  bottom: 10px;
  right: 10px;
  text-align: right;
  font-size: 12px;
  color: grey;
}
.header-notes {
  position: absolute;
  width: 100%;
  top: 60px;
  text-align: right;
  font-size: 12px;
  color: grey;
}



